import WebFont from 'webfontloader';

WebFont.load({
google: {
  families: ['Poppins:300,400,500,600,700,800,900']
},
active: function() {
  // Fonty jsou načteny a připraveny k použití
  document.documentElement.classList.add('fonts-loaded');
},
inactive: function() {
  // Fonty se nepodařilo načíst
  console.warn('Fonty se nepodařilo načíst.');
}
});
