import Splide from '@splidejs/splide';

const EL = '.js-gallery-slider';
const ACTIVE_CLASS = 'is-active';
const ELS = document.querySelectorAll(EL);

function isMobileDevice() {
  return window.innerWidth <= 768;
}

if (isMobileDevice()) {
  ELS.forEach((EL) => {
    new Splide(EL, {
      arrows: true,
      pagination: false,
    }).mount();
  });
}
