import Splide from '@splidejs/splide';

const EL = '.js-hero-carousel';
const ACTIVE_CLASS = 'is-active';
const ELS = document.querySelectorAll(EL);

ELS.forEach((element) => {
  new Splide(element, {
    type: 'fade',
    arrows: true,
    pagination: false,
    perPage: 1,
  }).mount();
});
