import Splide from '@splidejs/splide';

const EL = '.js-product-carousel';
const ACTIVE_CLASS = 'is-active';
const ELS = document.querySelectorAll(EL);

ELS.forEach((element) => {
  new Splide(element, {
    type: 'loop',
    arrows: true,
    pagination: false,
    perPage: 5,
    gap: 0,
    perMove: 1,
    breakpoints: {
      1366: {
        perPage: 3,
      },
      1024: {
        perPage: 2,
      },
    },
  }).mount();
});
